.sub-title {
  padding-top: 10px;
}
.sub-title > div {
    padding: 0 10px;
}
.sub-title span {
    padding: 5px;
    display: inline-block;
    margin-right: 5px;
    margin-bottom: 5px;
    background-color: #fff;
}
.tables .table-title {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.tables .table-title span {
    padding: 5px;
    display: inline-block !important;
    margin-right: 5px !important;
    margin-bottom: 5px !important;
    background-color: #fff;
}
