.area-list > div:nth-child(3), .area-list > div:nth-child(4) {
  margin-bottom: 10px;
  border-bottom: 1px solid #f2f2f2;
}
.area-list .brand-part div:nth-child(1) {
  background-color: rgba(255, 255, 255, 0.5);
}
.area-list .brand-part:nth-last-child(1) {
  background-color: rgba(255, 255, 255, 0.5);
}
