.main-nav i {
  display: none;
  width: 4.5rem;
  height: 4.5rem;
  border-radius: 999px;
  background-color: #f2f2f2;
  margin: 5px auto 15px;
  line-height: 4.5rem;
  color: #fff;
  font-style: normal;
  font-size: 2.5rem;
  text-align: center;
  box-shadow: 1px 1px 5px 1px rgba(206, 206, 206, 0.5);
}
.bg-red {
  background: linear-gradient(50deg, rgba(248, 94, 9, 0.5) 0%, #f85e09 100%);
}
.bg-yellow {
  background: linear-gradient(50deg, rgba(251, 229, 0, 0.5) 0%, #fbe500 100%);
}
.bg-green {
  background: linear-gradient(50deg, rgba(0, 255, 107, 0.5) 0%, #00ff6b 100%);
}
.bg-blue {
  background: linear-gradient(50deg, rgba(0, 57, 255, 0.5) 0%, #0039ff 100%);
}
.bg-orange {
  background: linear-gradient(50deg, rgba(255, 150, 0, 0.5) 0%, #ff9600 100%);
}
.bg-lightred {
  background: linear-gradient(50deg, rgba(255, 113, 143, 0.5) 0%, #ff718f 100%);
}
.day-list a {
  color: #fff;
}
.day-list a span {
    display: block;
    text-align: center;
    margin: 5px 0;
    font-size: 1.2rem;
    text-shadow: 1px 1px 1px #333;
}
.day-list a span:nth-child(1) {
      font-weight: bold;
}
.day-list a span:nth-child(2) {
      color: #fff;
}
.week-chart-list > div {
  height: 100px;
  margin: 10px;
  border: 1px solid #fff;
  background-color: rgba(255, 255, 255, 0.5);
}
